import React, { useContext, useEffect, useRef, useState } from "react";
import styled from "@emotion/styled";
import { keyframes } from "@emotion/react";
import { Context as IdContext } from "../../context/IdContext";
import { DescDetail } from "./DescDetail";

const fadeIn = keyframes`
  from { opacity: 0; transform: scale(0.95); }
  to { opacity: 1; transform: scale(1); }
`;

const DescriptionContainer = styled.div`
  position: fixed;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
  padding: 10px;
  width: 100%;
  max-width: 620px; // Aumentado de 520px a 620px
  max-height: 35vh;
  overflow-y: auto;
  z-index: 1000000;
  animation: ${fadeIn} 0.3s ease-out;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  &::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  @media (max-width: 768px) {
    width: 90%; // Aumentado de 80% a 90% para dispositivos mviles
    max-height: 45vh;
  }
`;

const CloseButton = styled.button`
  position: absolute;
  top: 5px;
  right: 5px;
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
`;

export const RentedDescription = ({
  pos,
  rented,
  onClose,
  onMouseEnter,
  isClicked,
}) => {
  const [isHovered, setIsHovered] = useState(true);

  const handleMouseLeave = () => {
    onClose();
  };

  const containerRef = useRef(null);
  const {
    state: { id },
    idMsg,
  } = useContext(IdContext);

 
  const calculatePosition = () => {
    const padding = 20; // Padding from the edges of the screen
    const containerWidth = 620; // Width of the container
    const containerHeight = window.innerHeight * 0.35; // 35% of viewport height

    let left = pos.x;
    let top = pos.y;

    // Adjust horizontal position
    if (left + containerWidth + padding > window.innerWidth) {
      left = window.innerWidth - containerWidth - padding;
    }
    if (left < padding) {
      left = padding;
    }

    // Adjust vertical position
    if (top + containerHeight + padding > window.innerHeight) {
      top = window.innerHeight - containerHeight - padding;
    }
    if (top < padding) {
      top = padding;
    }

    return { left, top };
  };

  const containerStyle = calculatePosition();
  const handleClick = (e) => {
    e.stopPropagation();
    // idMsg(false);
  };

  return (
    <DescriptionContainer
      style={containerStyle}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onClose}
      onClick={handleClick}
    >
      <CloseButton onClick={onClose}></CloseButton>
      <DescDetail
        name={id.name}
        phone={id.phone}
        email={id.email}
        end={id.start}
        start={id.end}
        room={id.room}
        adelanto={id.adelanto}
        comments={id.comments}
        dni={id.dni}
        montoPendiente={id.montoPendiente}
        nights={id.nights}
        id={id.id}
        nombre_recepcionista={id.nombre_recepcionista}
        time={id.time}
        precioTotal={id.precioTotal}
        price={id.price}
        paymentMethod={id.paymentMethod}
        numberOfGuests={id.numberOfGuests}
        guestNames={id.guestNames}
        roomType={id.roomType}
        isBooking={id.isBooking}
        surname={id.surname}
        billingStatus={id.billingStatus}
        housekeepingStatus={id.housekeepingStatus}
      />
    </DescriptionContainer>
  );
};
