import createDataContext from "./createDataContext";
import { io } from "socket.io-client";

const MSGS = {
  SOCKET_CONNECTION: "SOCKET_CONNECTION",
  SOCKET_DISCONNECTION: "SOCKET_DISCONNECTION",
};

const socketConnect = (dispatch) => () => {
  console.log("Attempting to connect socket");
  const userId = localStorage.getItem('userId');
  const socketUrl = process.env.REACT_APP_SOCKET_URL; // Access the socket URL from environment variables
  console.log(socketUrl)
  const newSocket = io("http://localhost:8000", {
    query: { userId },
    withCredentials: true,
  });

  newSocket.on('connect', () => {
    console.log('Socket connected successfully');
  });

  newSocket.on('connect_error', (error) => {
    console.error('Socket connection error:', error);
  });

  newSocket.on('error', (error) => {
    console.error('Socket error:', error);
  });

  dispatch({ type: MSGS.SOCKET_CONNECTION, userId });
};

const socketDisconnect = (dispatch) => () => {
  console.log("Attempting to disconnect socket");
  dispatch({ type: MSGS.SOCKET_DISCONNECTION });
};

const SocketContext = (state, msg) => {
  console.log("SocketContext reducer", msg.type, state);
  switch (msg.type) {
    case MSGS.SOCKET_CONNECTION:
      if (!state.socket) {
        console.log("Creating new socket connection");
        // const newSocket = io("http://localhost:8000", {
        const newSocket = io("/", { // Changed from "http://localhost:8000" to "/"
          withCredentials: true,
          query: { userId: msg.userId }
        });
        newSocket.on('connect', () => {
          console.log('Socket connected successfully');
        });
        newSocket.on('connect_error', (error) => {
          console.error('Socket connection error:', error);
        });
        return { ...state, socket: newSocket };
      }
      console.log("Socket already exists, skipping connection");
      return state;
    case MSGS.SOCKET_DISCONNECTION:
      if (state.socket) {
        console.log("Disconnecting existing socket");
        state.socket.disconnect();
        return { ...state, socket: null };
      }
      return state;
    default:
      return state;
  }
};

export const { Context, Provider } = createDataContext(
  SocketContext,
  {
    socketConnect,
    socketDisconnect,
  },
  {
    socket: null,
  }
);
