import React, { useState, useEffect, useContext } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import { Provider as SocketProvider, Context as SocketContext } from "./context/SocketContext";
import { Provider as IdProvider } from "./context/IdContext";
import { Provider as FormProvider } from "./context/FormContext";
import { Provider as DatesProvider } from "./context/DatesContext";
import { Provider as ResDataProvider } from "./context/ResDataContext";
import { Provider as RentedProvider } from "./context/RentedFormContext";
import { Provider as ThumbProvider } from "./context/ThumbContext"; // Add this import
import { DndProvider } from "react-dnd-multi-backend";
import { HTML5Backend } from "react-dnd-html5-backend";
import { TouchBackend } from "react-dnd-touch-backend";
import { MultiBackend, TouchTransition, MouseTransition } from "react-dnd-multi-backend";
import LoginForm from "./components/Form/LoginForm";
import { Container } from "./components/Container/Container";
import axios from './utils/axiosConfig';

const HTML5toTouch = {
  backends: [
    {
      id: "html5",
      backend: HTML5Backend,
      preview: true,
      transition: MouseTransition,
    },
    {
      id: "touch",
      backend: TouchBackend,
      options: { enableMouseEvents: true, delayTouchStart: 100 },
      preview: true,
      transition: TouchTransition,
    },
  ],
};

function AppContent() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userId, setUserId] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const { state, socketConnect, socketDisconnect } = useContext(SocketContext);

  const theme = createTheme({
    // Your theme options here
  });
  axios.defaults.withCredentials = true;

  useEffect(() => {
    const checkSession = async () => {
      try {
        const response = await axios.get('/check-session', { withCredentials: true });
        if (response.data.isLoggedIn) {
          setIsLoggedIn(true);
          setUserId(response.data.userId);
          localStorage.setItem('userId', response.data.userId);
          socketConnect();
        } else {
          setIsLoggedIn(false);
          setUserId(null);
        }
      } catch (error) {
        console.error('Error checking session:', error);
      }
      setIsLoading(false);
    };

    checkSession();
  }, []);

  const handleLogin = (userData) => {
    setIsLoggedIn(true);
    setUserId(userData.userId);
    localStorage.setItem('userId', userData.userId);
    socketConnect();
  };

  const handleLogout = () => {
    setIsLoggedIn(false);
    setUserId(null);
    localStorage.removeItem('userId');
    socketDisconnect();
  };

  if (isLoading) {
    return <div>Loading...</div>;
  }

  return (
    <ThemeProvider theme={theme}>
      <IdProvider>
        <FormProvider>
          <DatesProvider>
            <ResDataProvider>
              <RentedProvider>
                <ThumbProvider>
                  <DndProvider backend={MultiBackend} options={HTML5toTouch}>
                    <BrowserRouter>
                      <div className="">
                        <main className="">
                          <Routes>
                            <Route
                              path="/"
                              element={
                                isLoggedIn ? (
                                  <Navigate to="/app" replace />
                                ) : (
                                  <LoginForm onLogin={handleLogin} />
                                )
                              }
                            />
                            <Route
                              path="/app"
                              element={
                                isLoggedIn ? (
                                  <Container userId={userId} />
                                ) : (
                                  <Navigate to="/" replace />
                                )
                              }
                            />
                          </Routes>
                        </main>
                      </div>
                    </BrowserRouter>
                  </DndProvider>
                </ThumbProvider>
              </RentedProvider>
            </ResDataProvider>
          </DatesProvider>
        </FormProvider>
      </IdProvider>
    </ThemeProvider>
  );
}

export default function App() {
  return (
    <SocketProvider>
      <AppContent />
    </SocketProvider>
  );
}