import createDataContext from "./createDataContext";
import { addYears, formatWithOptions, format } from "date-fns/fp";
import { es } from "date-fns/locale";
import { u } from "react-uuid";
import { getStyleFromJSON } from "../utils/stylesUtils";
import {
  eachDayOfInterval,
  add,
  addDays,
  subDays,
  differenceInDays,
  getDaysInMonth,
  isFirstDayOfMonth,
  isLastDayOfMonth,
} from "date-fns";
import _ from "lodash";

const MSGS = {
  ADD_DAY: "ADD_DAY",
  SUB_DAY: "SUB_DAY",
  NEW_DAY: "NEW_DAY",
  NEW_INTERVAL_RENTED: "NEW_INTERVAL_RENTED",
  INTERVAL_RENTED_DB: "INTERVAL_RENTED_DB",
  START_DATE: "START_DATE",
  SEARCH_NAME: "SEARCH_NAME",
  REMOVE_BY_DRAGGING: "REMOVE_BY_DRAGGING",
  SEND_INTERVAL_FOR_SOCKET: "SEND_INTERVAL_FOR_SOCKET",
  EDITING: "EDITING",
  DELETE_INTERVAL_RENTED: "DELETE_INTERVAL_RENTED",
  SEARCH_ID: "SEARCH_ID",
  POS: "POS",
  CLICKED_RENTED: "CLICKED_RENTED",
  UPDATE_RENTED_STATUS: "UPDATE_RENTED_STATUS",
  SELECTED_ROOMS: "SELECTED_ROOMS",
};

const updateRentedStatusMsg =
  (dispatch) => (id, billingStatus, housekeepingStatus) => {
    console.log(id, billingStatus, housekeepingStatus);
    dispatch({
      type: MSGS.UPDATE_RENTED_STATUS,
      id,
      billingStatus,
      housekeepingStatus,
    });
  };
