export const EditForm = () => {
  const { editingMsg } = useContext(DatesContext);

  const [anchorElRoomType, setAnchorElRoomType] = useState(null);
  const [anchorElPaymentMethod, setAnchorElPaymentMethod] = useState(null);
  const [guestInfo, setGuestInfo] = useState([]);
  const [showGuestFields, setShowGuestFields] = useState(false);

  // const handleCloseForm = () => {
  //   showFormMsg(false); // Close the form
  // };

  //   billingStatus: rented.billingStatus || 'pendiente',
  // housekeepingStatus: rented.housekeepingStatus || 'pendiente',

  // Add these handlers

  const handleClickRoomType = (event) => {
    setAnchorElRoomType(event.currentTarget);
  };

  const handleCloseRoomType = () => {
    setAnchorElRoomType(null);
  };

  const handleClickPaymentMethod = (event) => {
    setAnchorElPaymentMethod(event.currentTarget);
  };

  const handleClosePaymentMethod = () => {
    setAnchorElPaymentMethod(null);
  };

  const roomPrices = {
    individual: 30000,
    doble: 40000,
    triple: 60000,
    cuadruple: 70000,
  };
  // you can also target specific fields by their names
  const {
    state: {
      calendar: { intervalRentedArray },
    },
  } = useContext(DatesContext);
  const { rented } = useContext(RentedContext).state;
  const { rentedMsg } = useContext(RentedContext);

  const { intervalRented, userId } = useContext(FormContext).state;
  const { showEditForm, start, end, room } = rented;
  // const { start, end, room, formType } = intervalRented;

  const startDate = new Date(end);
  const formattedStartDate = startDate.toISOString().split("T")[0];
  const endDate = new Date(start);
  const formattedEndDate = endDate.toISOString().split("T")[0];
  const date = new Date("1970-01-01T00:00:00.009Z");
  const formattedTime = format(date, "HH:mm");
  console.log(rented)
  const {
    handleSubmit,
    control,
    watch,
    setValue,
    setError,
    clearErrors,
    formState: { errors },
  } = useForm({
    defaultValues: {
      checkIn: formattedStartDate,
      checkOut: formattedEndDate,
      phone: rented.phone,
      nights: differenceInDays(endDate, startDate),
      roomNumber: rented.room,
      name: rented.name,
      email: rented.email,
      nombre_recepcionista: rented.nombre_recepcionista,
      price: rented.price,
      precioTotal: rented.precioTotal,
      comments: rented.comments,
      adelanto: rented.adelanto,
      montoPendiente: rented.montoPendiente,
      dni: rented.dni,
      paymentMethod: rented.paymentMethod || "efectivo",
      numberOfGuests: rented.numberOfGuests || 1,
      guestNames: rented.guestNames || [""],
      roomType: rented.roomType || "individual",
      isBooking: rented.isBooking || false,
      surname: rented.surname || "",
      paymentStatus: rented.paymentStatus,
      billingStatus: rented.billingStatus || "pendiente",
      housekeepingStatus: rented.housekeepingStatus || "pendiente",
      selectedRooms: rented.room ? (Array.isArray(rented.room) ? rented.room : [rented.room]) : [],
    },
  });

  const [
    adelanto,
    checkIn,
    checkOut,
    comments,

    dni,
    email,

    montoPendiente,
    name,
    nights,
    nombre_recepcionista,
    phone,
    precioTotal,
    price,
    roomNumber,

    time,
    paymentMethod,
    numberOfGuests,
    guestNames,
    roomType,
    surname,
    billingStatus,
    housekeepingStatus,
    selectedRooms,
  ] = watch([
    "adelanto",
    "checkIn",
    "checkOut",
    "comments",
    "dni",
    "email",
    "empresa",
    "montoPendiente",
    "name",
    "nights",
    "nombre_recepcionista",
    "phone",
    "precioTotal",
    "price",
    "roomNumber",
    "time",
    "paymentMethod",
    "numberOfGuests",
    "guestNames",
    "roomType",
    "surname",
    "billingStatus",
    "housekeepingStatus",
    "selectedRooms",
  ]);
  const handlePaymentStatusChange = (value) => {
    setValue("paymentStatus", value);
  };
  const PaymentStatusSelect = ({ control, value, onChange }) => {
    return (
      <Controller
        name="paymentStatus"
        control={control}
        render={({ field }) => (
          <select {...field} onChange={(e) => onChange(e.target.value)}>
            <option value="unpaid">Adeudada</option>
            <option value="partially_paid">Parcialmente Paga</option>
            <option value="fully_paid">Paga</option>
          </select>
        )}
      />
    );
  };
  const handleBillingStatusChange = (value) => {
    setValue("billingStatus", value);
  };

  const handleHousekeepingStatusChange = (value) => {
    setValue("housekeepingStatus", value);
  };
  const handleEmailChange = (newValue) => {
    const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
    setValue("email", newValue);
    if (!emailRegex.test(newValue)) {
      setError("email", {
        type: "pattern",
        message: "Invalid email address",
      });
    } else {
      clearErrors("email");
    }
  };
  const handleNombreRecepcionistaChange = (newValue) => {
    setValue("nombre_recepcionista", newValue);
  };
  const handleRoomTypeChange = (value, isBooking) => {
    setValue("roomType", value);
    setValue("isBooking", isBooking);
    const newPrice = roomPrices[value];
    const bookingFee = isBooking ? newPrice * 0.1 : 0;
    setValue("price", newPrice + bookingFee);

    const nights = parseInt(watch("nights"), 10);
    const newPrecioTotal = (newPrice + bookingFee) * nights;
    setValue("precioTotal", newPrecioTotal);

    const adelanto = parseFloat(watch("adelanto") || 0);
    const newMontoPendiente = newPrecioTotal - adelanto;
    setValue("montoPendiente", newMontoPendiente);

    handleCloseRoomType();
  };

  const handlePaymentMethodChange = (value) => {
    setValue("paymentMethod", value);
    handleClosePaymentMethod();
  };

  const handleNumberOfGuestsChange = (value) => {
    const guests = parseInt(value, 10);
    setValue("numberOfGuests", guests);
    const currentNames = watch("guestNames");
    if (guests > currentNames.length) {
      setValue("guestNames", [
        ...currentNames,
        ...Array(guests - currentNames.length).fill(""),
      ]);
    } else {
      setValue("guestNames", currentNames.slice(0, guests));
    }
  };

  const handleGuestNameChange = (index, value) => {
    const currentNames = [...watch("guestNames")];
    currentNames[index] = value;
    setValue("guestNames", currentNames);
  };

  const handleCheckInChange = (value) => {
    setValue("checkIn", value);
    const newNights = differenceInDays(
      new Date(watch("checkOut")),
      new Date(value)
    );
    console.log(value, newNights);
    setValue("nights", newNights);

    // setEndNights({ ...endNights, checkIn: value, active: false });
  };

  const handleCheckOutChange = (value) => {
    setValue("checkOut", value);
    // Calculate new nights based on the new checkOut value and the current checkIn value
    const newNights = differenceInDays(
      new Date(value),
      new Date(watch("checkIn"))
    );
    setValue("nights", newNights); // Update the nights based on the new checkOut date
    // setEndNights({ ...endNights, checkOut: value, active: false });
    console.log(value, newNights);
  };
  const handleNightsChange = (nights) => {
    const nightsValue = parseInt(nights, 10); // Ensure the value is treated as a number
    if (nightsValue < 1) {
      setError("nights", {
        type: "valueError",
        message: "Number of nights must be at least 1",
      });
    } else {
      clearErrors("nights"); // Clear the error if the value is valid
      const newEndDate = add(new Date(watch("checkIn")), { days: nightsValue });
      const formattedEndDate = newEndDate.toISOString().split("T")[0];

      setValue("checkOut", formattedEndDate);
      console.log(nights, formattedEndDate);
      // setEndNights({ checkOut: formattedEndDate, active: true });
    }
  };

  const handleGuestInfoClick = () => {
    setShowGuestFields(!showGuestFields);
  };

  const handleGuestInfoChange = (index, value) => {
    const newGuestInfo = [...guestInfo];
    newGuestInfo[index] = value;
    setGuestInfo(newGuestInfo);
  };

  const handleGuestInfoSave = () => {
    setValue("guestNames", guestInfo);
    setShowGuestFields(false);
  };

  // const handleNumberOfGuestsChange = (value) => {
  //   const guests = parseInt(value, 10);
  //   setValue("numberOfGuests", guests);
  //   const currentNames = watch("guestNames");
  //   if (guests > currentNames.length) {
  //     setValue("guestNames", [
  //       ...currentNames,
  //       ...Array(guests - currentNames.length).fill(""),
  //     ]);
  //   } else {
  //     setValue("guestNames", currentNames.slice(0, guests));
  //   }
  // };
  const formatPhoneInput = (input) => {
    const digits = input.replace(/\D/g, ""); // Remove non-digit characters
    if (digits.length > 6) {
      return `${digits.slice(0, 2)}-${digits.slice(2, 6)}-${digits.slice(6)}`;
    } else if (digits.length > 2) {
      return `${digits.slice(0, 2)}-${digits.slice(2)}`;
    }
    return digits;
  };

  const handleChangePricePerNight = () => {
    const totalPrice = parseFloat(watch("precioTotal"));
    const nights = parseFloat(watch("nights"));
    if (nights > 0) {
      const pricePerNight = totalPrice / nights;
      setValue("price", pricePerNight);
    }
  };
  const handlePrecioTotalChange = (newPrecioTotal) => {
    const precioTotalValue = parseFloat(newPrecioTotal);
    if (precioTotalValue < 0) {
      setError("precioTotal", {
        type: "valueError",
        message: "Total price must be non-negative",
      });
    } else {
      clearErrors("precioTotal");
      setValue("precioTotal", precioTotalValue);
    }
  };

  const handleadelantoChange = (value) => {
    const adelanto = parseFloat(value || 0);
    const total = parseFloat(watch("precioTotal") || 0);
    const montoPendiente = total - adelanto;
    setValue("adelanto", adelanto); // Actualizar el valor de adelanto
    setValue("montoPendiente", montoPendiente);
  };
  const formatPhoneNumber = (input) => {
    // Remove all non-digit characters
    const digits = input.replace(/\D/g, "");

    // Format for Argentinian local number (3487550568)
    if (digits.length <= 4) {
      return digits;
    } else if (digits.length <= 7) {
      return `${digits.slice(0, 4)}-${digits.slice(4)}`;
    } else if (digits.length <= 10) {
      return `${digits.slice(0, 4)}-${digits.slice(4, 7)}-${digits.slice(7)}`;
    } else {
      // For international numbers
      return `+${digits.slice(0, digits.length - 10)}-${digits.slice(
        -10,
        -6
      )}-${digits.slice(-6, -3)}-${digits.slice(-3)}`;
    }
  };

  const validatePhoneNumber = (phoneNumber) => {
    // Remove all non-digit characters for validation
    const digits = phoneNumber.replace(/\D/g, "");

    // Valid if it's 10 digits (local format) or more (for international numbers)
    return digits.length >= 10;
  };

  const handlePhoneChange = (value) => {
    const formattedValue = formatPhoneNumber(value);
    setValue("phone", formattedValue);

    if (!validatePhoneNumber(formattedValue)) {
      setError("phone", {
        type: "validation",
        message: "Please enter a valid phone number with at least 10 digits",
      });
    } else {
      clearErrors("phone");
    }
  };
  const handleRoomChange = (value) => {
    const roomNumber = parseInt(value, 10); // Convert value to an integer
    if (roomNumber < 1 || roomNumber > 15) {
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
      setValue("roomNumber", value); // Only set the value if it's within the valid range
    }
  };
  const handleNameChange = (value) => {
    setValue("name", value);
  };
  const handleSurnameChange = (value) => {
    setValue("surname", value);
  };
  const handleMontoPendienteChange = () => {
    const precioTotal = parseFloat(watch("precioTotal") || 0);
    const adelanto = parseFloat(watch("adelanto") || 0);
    const montoPendiente = precioTotal - adelanto;
    setValue("montoPendiente", montoPendiente);
  };
  // useEffect to update values based on certain conditions
  useEffect(() => {
    const nights = parseInt(watch("nights"), 10);
    const price = parseFloat(watch("price"));
    const precioTotal = parseFloat(watch("precioTotal"));

    if (!isNaN(nights) && !isNaN(price)) {
      // Update precioTotal when nights or price changes
      const newPrecioTotal = nights * price;
      setValue("precioTotal", newPrecioTotal);
    } else if (!isNaN(nights) && !isNaN(precioTotal)) {
      // Update price when nights or precioTotal changes
      const newPrice = precioTotal / nights;
      setValue("price", newPrice);
    }

    // Recalculate montoPendiente
    const adelanto = parseFloat(watch("adelanto") || 0);
    const newMontoPendiente = (precioTotal || 0) - adelanto;
    setValue("montoPendiente", newMontoPendiente);
  }, [
    watch("nights"),
    watch("price"),
    watch("precioTotal"),
    watch("adelanto"),
    setValue,
  ]);
  useEffect(() => {
    const newPrice = parseFloat(watch("price"));
    const newNights = parseFloat(watch("nights"));
    const newprecioTotal = newPrice * newNights;
    setValue("precioTotal", newprecioTotal);

    // Recalculate montoPendiente right after updating precioTotal
    const adelanto = parseFloat(watch("adelanto") || 0);
    const montoPendiente = newprecioTotal - adelanto;
    setValue("montoPendiente", montoPendiente);
  }, [watch("price"), watch("nights"), watch("adelanto"), setValue]);
  useEffect(() => {
    const roomNumber = watch("roomNumber");

    // Example validation: Check if roomNumber is within a specific range
    if (roomNumber < 1 || roomNumber > 100) {
      // Assuming rooms are numbered 1-100
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
    }
  }, [watch("roomNumber"), setError, clearErrors]);

  useEffect(() => {
    const checkInDate = new Date(watch("checkIn"));
    const checkOutDate = new Date(watch("checkOut"));
    const nights = parseInt(watch("nights"), 10);
    const expectedCheckOutDate = add(checkInDate, { days: nights });

    if (
      isAfter(checkOutDate, checkInDate) &&
      isSameDay(expectedCheckOutDate, checkOutDate)
    ) {
      // Clear errors if the conditions are met
      clearErrors(["checkIn", "checkOut", "nights"]);
    } else {
      // Set specific error messages for each case
      if (!isAfter(checkOutDate, checkInDate)) {
        setError("checkIn", {
          type: "dateRange",
          message: "Check-in date must be before check-out date.",
        });
      }
      if (!isSameDay(expectedCheckOutDate, checkOutDate)) {
        setError("nights", {
          type: "dateMismatch",
          message: "Number of nights does not match the dates selected.",
        });
      }
    }
  }, [
    watch("checkIn"),
    watch("checkOut"),
    watch("nights"),
    setError,
    clearErrors,
  ]);
  const handleCommentsChange = (value) => {
    setValue("comments", value);
  };
  useEffect(() => {
    const currentComments = watch("comments");

    if (currentComments.length > 500) {
      setError("comments", {
        type: "maxLength",
        message: "Comment is too long. Maximum length is 500 characters.",
      });
    } else {
      clearErrors("comments");
    }
  }, [watch("comments"), setError, clearErrors]);
  const handleDeleteReservation = async () => {
    // Ensure userId is available
    const currentUserId = localStorage.getItem('userId');

    if (!currentUserId) {
      console.error("User ID not found");
      return;
    }

    const requestUrl = `/delete-reservation/${rented.id}?userId=${currentUserId}`;

    try {
      const res = await axios.delete(requestUrl);

      if (res.status === 200) {
        rentedMsg({ ...rented, showEditForm: false });
        console.log("Reservation deleted successfully");
      }
    } catch (error) {
      console.error("Error deleting reservation:", error);
      // Add more detailed error logging
      if (error.response) {
        console.error("Response data:", error.response.data);
        console.error("Response status:", error.response.status);
      }
    }
  };
  const onSubmit = async (data) => {
    const {
      checkIn,
      checkOut,
      nights,
      name,
      email,
      phone,
      selectedRooms,
      price,
      precioTotal,
      adelanto,
      comments,
      montoPendiente,
      paymentMethod,
      numberOfGuests,
      guestNames,
      roomType,
      isBooking,
      surname,
      billingStatus,
      housekeepingStatus,
      dni,

      nombre_recepcionista
    } = data;

    const nightsDifference = differenceInDays(
      new Date(checkOut),
      new Date(checkIn)
    );

    if (nightsDifference !== parseInt(nights, 10)) {
      setError("nights", {
        type: "dateMismatch",
        message: "Number of nights does not match the dates selected.",
      });
      return;
    }

    // Separate original rooms from new rooms
    const originalRooms = Array.isArray(rented.room) ? rented.room : [rented.room];
    const newRooms = selectedRooms.filter(room => !originalRooms.includes(room));

    // Check overlap only for new rooms
    const isOverlapping = intervalRentedArray.some((interval) => {
      const newStart = new Date(checkIn);
      const newEnd = new Date(checkOut);
      const existingStart = new Date(interval.start);
      const existingEnd = new Date(interval.end);

      // Check if any of the new rooms overlap with the existing reservation
      const sameRoom = newRooms.includes(interval.room);

      const overlapCondition = (
        (newStart < existingEnd && newEnd > existingStart) ||
        (existingStart < newEnd && existingEnd > newStart)
      );

      return sameRoom && overlapCondition && interval.id !== rented.id; // Exclude the current reservation
    });

    console.log("Is overlapping:", isOverlapping);
    console.log("Original rooms:", originalRooms);
    console.log("New rooms:", newRooms);

    if (!isOverlapping) {
      let convertedCheckin = checkIn.split("-").join("/");
      let convertedChecOut = checkOut.split("-").join("/");
      const requestUrl = `/update-reservation/${rented.id}`;
      const precioTotalNumber = parseFloat(precioTotal);

      try {
        const res = await axios.put(requestUrl, {
          name,
          email,
          phone,
          userId: localStorage.getItem('userId'),
          rooms: originalRooms, // Send original rooms for the main reservation
          newRooms: newRooms, // Send only new rooms
          start: convertedCheckin,
          end: convertedChecOut,
          isOverlapping: "false",
          price,
          nights,
          comments,
          precioTotal: precioTotalNumber,
          adelanto,
          time: new Date().toISOString(),
          montoPendiente,
          dni,
          paymentMethod,
          numberOfGuests,
          guestNames,
          roomType,
          isBooking,
          surname,
          billingStatus,
          housekeepingStatus,
          nombre_recepcionista
        });

        if (res.status === 200) {
          const { reservations } = res.data;
          editingMsg(reservations); // Pass all updated and new reservations
          rentedMsg({ ...rented, showEditForm: false });
          console.log("Reservation updated successfully");
          console.log("Updated reservation data:", res.data);
        }
      } catch (error) {
        console.error("Error updating reservation:", error);
        if (error.response) {
          console.error("Response data:", error.response.data);
          console.error("Response status:", error.response.status);
        }
      }
    } else {
      setError("checkIn", {
        type: "overlap",
        message: "The selected dates and new rooms overlap with an existing reservation.",
      });
    }
  };

  const billingStatusOptions = [
    { value: "pendiente", label: "Pendiente" },
    { value: "pagado", label: "Pagado" },
    { value: "con-sena", label: "Con sea" },
    { value: "no-pagado", label: "No pagado" },
  ];

  const housekeepingStatusOptions = [
    { value: "pendiente", label: "Pendiente" },
    { value: "limpio", label: "Limpio" },
    { value: "reparacion", label: "Reparacin" },
    { value: "faltan-fundas", label: "Faltan fundas" },
    { value: "faltan-sabanas", label: "Faltan sbanas" },
    { value: "falta-limpiar", label: "Falta limpiar" },
  ];

  console.log("Selected rooms:", selectedRooms);

  useEffect(() => {
    console.log("Selected rooms:", watch("selectedRooms"));
  }, [watch("selectedRooms")]);

  return (
    <div className="formWrapper">
      <form onSubmit={handleSubmit(onSubmit)} className="formContainer">
        <button
          onClick={() => rentedMsg({ ...rented, showEditForm: false })}
          style={{
            position: "absolute",
            top: "20px",
            right: "20px",
            background: "orange",
          }}
          aria-label="Close form"
        >
          <CloseIcon />
        </button>

        <div className="formColumn">
          <h3>Informacin del Husped</h3>
          <InputForm control={control} name="name" type="text" label="Nombre" value={watch("name")} handleChange={handleNameChange} />
          <InputForm control={control} name="surname" type="text" label="Apellido" value={watch("surname")} handleChange={handleSurnameChange} />
          <InputForm control={control} name="email" type="email" label="Email" value={watch("email")} handleChange={handleEmailChange} />
          <InputForm control={control} name="phone" type="tel" label="Telfono" value={watch("phone")} handleChange={(value) => setValue("phone", value)} />
          <InputForm control={control} name="dni" type="text" label="DNI" value={watch("dni")} handleChange={(value) => setValue("dni", value)} />
        </div>

        <div className="formColumn">
          <h3>Detalles de la Reserva</h3>
          <InputForm control={control} name="checkIn" type="date" label="Fecha de Entrada" value={watch("checkIn")} handleChange={handleCheckInChange} />
          <InputForm control={control} name="checkOut" type="date" label="Fecha de Salida" value={watch("checkOut")} handleChange={handleCheckOutChange} />
          <InputForm control={control} name="nights" type="number" label="Nmero de Noches" value={watch("nights")} handleChange={handleNightsChange} />
          <InputForm control={control} name="numberOfGuests" type="number" label="Nmero de huspedes" value={watch("numberOfGuests")} handleChange={handleNumberOfGuestsChange} onGuestInfoClick={handleGuestInfoClick} />
          <InputForm
            control={control}
            name="selectedRooms"
            type="select"
            label="Habitaciones"
            value={watch("selectedRooms")}
            options={Array.from({ length: 15 }, (_, i) => ({ value: i + 1, label: i + 1 }))}
            handleChange={(value) => setValue("selectedRooms", value)}
            multiple={true}
            renderValue={(selected) => (
              <div className="selectedRoomsContainer">
                {selected.map((value) => (
                  <Chip key={value} label={value} className="roomChip" />
                ))}
              </div>
            )}
          />
          <InputForm
            control={control}
            name="roomType"
            type="select"
            label="Tipo de Habitacin"
            value={watch("roomType")}
            options={[
              { value: "individual", label: "Individual" },
              { value: "doble", label: "Doble" },
              { value: "triple", label: "Triple" },
              { value: "cuadruple", label: "Cuadruple" }
            ]}
            handleChange={(value) => handleRoomTypeChange(value, false)}
          />
        </div>

        <div className="formColumn">
          <h3>Pago y Habitacin</h3>
          <InputForm control={control} name="price" type="number" label="Precio" value={watch("price")} readOnly handleChange={() => { }} />
          <InputForm control={control} name="precioTotal" type="number" label="Precio Total" value={watch("precioTotal")} handleChange={handlePrecioTotalChange} />
          <InputForm control={control} name="adelanto" type="number" label="Adelanto" value={watch("adelanto")} handleChange={handleadelantoChange} />
          <InputForm control={control} name="montoPendiente" type="number" label="Monto Pendiente" value={watch("montoPendiente")} readOnly handleChange={handleMontoPendienteChange} />
          <div className="roomTypePaymentMethod">
            <div className="room-type">
              <InputForm control={control} name="roomType" type="select" label="Tipo de Habitacin" value={watch("roomType")} options={[{ value: "individual", label: "Individual" }, { value: "doble", label: "Doble" }, { value: "triple", label: "Triple" }, { value: "cuadruple", label: "Cuadruple" }]} handleChange={(value) => handleRoomTypeChange(value, false)} />
            </div>
            <div className="payment-method">
              <InputForm control={control} name="paymentMethod" type="select" label="Mtodo de Pago" value={watch("paymentMethod")} options={[{ value: "efectivo", label: "Efectivo" }, { value: "tarjeta", label: "Tarjeta" }, { value: "transferencia", label: "Transferencia" }]} handleChange={handlePaymentMethodChange} />
            </div>
          </div>
        </div>

        <div className="formRow fullWidth">
          <InputForm control={control} name="billingStatus" type="select" label="Estado de Facturacin" value={watch("billingStatus")} options={billingStatusOptions} handleChange={(value) => handleBillingStatusChange(value)} />
          <InputForm control={control} name="housekeepingStatus" type="select" label="Estado de Limpieza" value={watch("housekeepingStatus")} options={housekeepingStatusOptions} handleChange={(value) => handleHousekeepingStatusChange(value)} />
          <InputForm control={control} name="nombre_recepcionista" type="text" label="Nombre Recepcionista" value={watch("nombre_recepcionista")} handleChange={handleNombreRecepcionistaChange} />
        </div>

        <div className="formRow fullWidth comments-section">
          <InputForm control={control} name="comments" type="textarea" label="Comentarios" multiline rows={3} value={watch("comments")} handleChange={handleCommentsChange} />
        </div>

        <div className="submit-button-container fullWidth">
          <button type="button" onClick={handleDeleteReservation} className="deleteButton">
            Borrar Reserva
          </button>
          <button className="submit-button" type="submit">Confirmar</button>
        </div>
      </form>
    </div>
  );
};
