export const getStyleFromJSON = ({ billingStatus, housekeepingStatus }) => {
  const paymentStyle = styleJSON.statusStyles.paymentStatus[billingStatus];
  const cleaningStyle = styleJSON.statusStyles.cleaningStatus[housekeepingStatus];

  if (paymentStyle && cleaningStyle) {
    return {
      backgroundColor: `linear-gradient(135deg, ${paymentStyle.backgroundColor} 50%, ${cleaningStyle.backgroundColor} 50%)`,
      leftColor: paymentStyle.backgroundColor,
      rightColor: cleaningStyle.backgroundColor,
      color: "#000000", // Default black text color
      borderColor: "#000000", // Default black border color
    };
  } else if (paymentStyle) {
    return {
      backgroundColor: paymentStyle.backgroundColor,
      leftColor: paymentStyle.backgroundColor,
      rightColor: paymentStyle.backgroundColor,
      color: "#000000",
      borderColor: "#000000",
    };
  } else if (cleaningStyle) {
    return {
      backgroundColor: cleaningStyle.backgroundColor,
      leftColor: cleaningStyle.backgroundColor,
      rightColor: cleaningStyle.backgroundColor,
      color: "#000000",
      borderColor: "#000000",
    };
  } else {
    // If no matching styles found, return a default style
    return {
      backgroundColor: "#D3D3D3", // Light grey default
      leftColor: "#D3D3D3",
      rightColor: "#D3D3D3",
      color: "#000000",
      borderColor: "#000000",
    };
  }
};
