// import React, { useEffect, useState, useRef, useCallback } from "react";
import axios from "../../utils/axiosConfig"; // Use the configured axios instance
import { Context as FormContext } from "../../context/FormContext";
import { Context as DatesContext } from "../../context/DatesContext";
import { useContext } from "react";
// import "./FormStyles.css";
import { areIntervalsOverlapping } from "date-fns";
import React, { useEffect, useState } from "react";
import DatePicker from "react-datepicker"; // Add this import
import "react-datepicker/dist/react-datepicker.css"; // Add this import for styles
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
// import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
// import MenuItem from "@mui/material/MenuItem";
import {
  add,
  differenceInDays,
  isSameDay,
  format,
  addDays,
  subDays,
} from "date-fns";
import { isBefore, isAfter, isEqual } from "date-fns";
import MenuItem from "@mui/material/MenuItem";
import Button from "@mui/material/Button";
import ButtonGroup from "@mui/material/ButtonGroup";
import { InputForm } from "./NewForm";
import { useForm, Controller } from "react-hook-form";
import "./FormGridStyles.css";
import CloseIcon from "@mui/icons-material/Close"; // Import CloseIcon from Material-UI icons
import TextField from "@mui/material/TextField";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";

import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import { is } from "date-fns/locale";
import Select from "@mui/material/Select";
import { InputLabel } from "@mui/material";
import Chip from '@mui/material/Chip';

export const NewFormm = () => {
  const [anchorElRoomType, setAnchorElRoomType] = useState(null);
  const [anchorElPaymentMethod, setAnchorElPaymentMethod] = useState(null);
  const [showGuestFields, setShowGuestFields] = useState(false);
  const [guestInfo, setGuestInfo] = useState([]);

  const handleCloseForm = () => {
    showFormMsg(false); // Close the form
  };

  const handleClickRoomType = (event) => {
    console.log(event);
    setAnchorElRoomType(event.currentTarget);
  };

  const handleCloseRoomType = () => {
    setAnchorElRoomType(null);
  };

  const handleClickPaymentMethod = (event) => {
    setAnchorElPaymentMethod(event.currentTarget);
  };

  const handleClosePaymentMethod = () => {
    setAnchorElPaymentMethod(null);
  };
  // you can also target specific fields by their names
  const {
    state: {
      calendar: { intervalRentedArray },
    },
  } = useContext(DatesContext);
  const { showFormMsg } = useContext(FormContext);
  const { intervalRented } = useContext(FormContext).state;
  const userId = localStorage.getItem('userId'); // Use this line instead of getting it from context

  console.log(userId);
  const { start, end, room, formType } = intervalRented;

  const startDate = new Date(start);
  const formattedStartDate = startDate.toISOString().split("T")[0];
  const endDate = new Date(end);
  const formattedEndDate = endDate.toISOString().split("T")[0];
  const getCurrentDateTime = () => {
    const now = new Date();
    return format(now, "yyyy-MM-dd HH:mm:ss");
  };

  const [currentDateTime, setCurrentDateTime] = useState(getCurrentDateTime());

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentDateTime(getCurrentDateTime());
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const roomPrices = {
    individual: 30000,
    doble: 40000,
    triple: 60000,
    cuadruple: 70000,
  };
  const {
    handleSubmit,
    control,
    watch,
    setValue,
    setError,
    clearErrors,
    formState: { errors },
  } = useForm({
    defaultValues: {
      checkIn: formattedStartDate,
      checkOut: formattedEndDate,
      phone: "3487556765",
      nights: differenceInDays(endDate, startDate),
      roomNumber: room,
      name: "gerardo",
      surname: "bouza",
      email: "gon@gmail.com",
      price: roomPrices["doble"],
      precioTotal: 45000,
      comments: "what?",
      adelanto: 0,
      montoPendiente: 0,
      dni: 24916296,
      nombre_recepcionista: "nadie por ahi?",
      paymentMethod: "efectivo",
      numberOfGuests: 1,
      guestNames: [""],
      roomType: "doble",
      isBooking: false,
      paymentStatus: "inpago",
      billingStatus: "pendiente",
      housekeepingStatus: "pendiente",
      selectedRooms: [room],
    },
  });

  const [
    adelanto,
    checkIn,
    checkOut,
    comments,
    dni,
    email,
    montoPendiente,
    name,
    surname,
    nights,
    nombre_recepcionista,
    phone,
    precioTotal,
    price,
    roomNumber,
    time,
    paymentMethod,
    numberOfGuests,
    guestNames,
    roomType,
    isBooking,
    billingStatus,
    housekeepingStatus,
    selectedRooms,
  ] = watch([
    "adelanto",
    "checkIn",
    "checkOut",
    "comments",
    "dni",
    "email",
    "montoPendiente",
    "name",
    "surname",
    "nights",
    "nombre_recepcionista",
    "phone",
    "precioTotal",
    "price",
    "roomNumber",
    "time",
    "paymentMethod",
    "numberOfGuests",
    "guestNames",
    "roomType",
    "isBooking",
    "billingStatus",
    "housekeepingStatus",
    "selectedRooms",
  ]);

  const handleGuestNameChange = (index, value) => {
    const currentNames = [...watch("guestNames")];
    currentNames[index] = value;
    setValue("guestNames", currentNames);
  };
  const handleGuestInfoClick = (event) => {
    setValue("numberOfGuests", event.currentTarget);
  };

  const handleBillingStatusChange = (value) => {
    setValue("billingStatus", value);
  };

  const handleHousekeepingStatusChange = (value) => {
    setValue("housekeepingStatus", value);
  };

  const handleNumberOfGuestsChange = (value) => {
    const guests = parseInt(value, 10);
    setValue("numberOfGuests", guests);
    const currentNames = watch("guestNames");
    if (guests > currentNames.length) {
      setValue("guestNames", [
        ...currentNames,
        ...Array(guests - currentNames.length).fill(""),
      ]);
    } else {
      setValue("guestNames", currentNames.slice(0, guests));
    }
  };

  const handleRoomTypeChange = (value, isBooking) => {
    setValue("roomType", value);
    setValue("isBooking", isBooking);
    const newPrice = roomPrices[value];
    const bookingFee = isBooking ? newPrice * 0.1 : 0; // 10% booking fee, ajusta segn necesites
    setValue("price", newPrice + bookingFee);

    // Recalcular el precio total
    const nights = parseInt(watch("nights"), 10);
    const newPrecioTotal = (newPrice + bookingFee) * nights;
    setValue("precioTotal", newPrecioTotal);

    // Recalcular el monto pendiente
    const adelanto = parseFloat(watch("adelanto") || 0);
    const newMontoPendiente = newPrecioTotal - adelanto;
    setValue("montoPendiente", newMontoPendiente);

    handleCloseRoomType();
  };
  const handleEmailChange = (newValue) => {
    const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
    setValue("email", newValue);
    if (!emailRegex.test(newValue)) {
      setError("email", {
        type: "pattern",
        message: "Invalid email address",
      });
    } else {
      clearErrors("email");
    }
  };

  const handlePaymentMethodChange = (value) => {
    setValue("paymentMethod", value);
  };
  // Handle change function for the "nombre_recepcionista" field
  const handleNombreRecepcionistaChange = (newValue) => {
    setValue("nombre_recepcionista", newValue);
  };

  const handleCheckInChange = (date) => {
    setValue("checkIn", date);
    const checkInDate = new Date(date);
    const checkOutDate = new Date(watch("checkOut"));

    if (
      checkInDate instanceof Date &&
      !isNaN(checkInDate) &&
      checkOutDate instanceof Date &&
      !isNaN(checkOutDate)
    ) {
      // Calcular el nuevo nmero de noches
      const newNights = differenceInDays(checkOutDate, checkInDate);

      if (newNights >= 1) {
        setValue("nights", newNights);

        // Recalcular precioTotal
        const price = parseFloat(watch("price"));
        if (!isNaN(price)) {
          setValue("precioTotal", price * newNights);
        }
      } else {
        // Si la nueva fecha de ingreso es posterior a la de egreso, ajustar la fecha de egreso
        const newCheckOutDate = addDays(checkInDate, 1);
        const formattedCheckOut = format(newCheckOutDate, "yyyy-MM-dd");
        setValue("checkOut", formattedCheckOut);
        setValue("nights", 1);

        // Recalcular precioTotal para una noche
        const price = parseFloat(watch("price"));
        if (!isNaN(price)) {
          setValue("precioTotal", price);
        }
      }
    }
  };

  const handleCheckOutChange = (date) => {
    setValue("checkOut", date);
    const newNights = differenceInDays(
      new Date(date),
      new Date(watch("checkIn"))
    );
    setValue("nights", newNights);
    console.log(date, newNights);
  };

  const handleadelantoChange = (value) => {
    const adelanto = parseFloat(value || 0);
    const total = parseFloat(watch("precioTotal") || 0);
    const montoPendiente = total - adelanto;
    setValue("adelanto", adelanto); // Actualizar el valor de adelanto
    setValue("montoPendiente", montoPendiente);
  };
  const handleRoomChange = (value) => {
    const roomNumber = parseInt(value, 10); // Convert value to an integer
    if (roomNumber < 1 || roomNumber > 15) {
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
      setValue("roomNumber", value); // Only set the value if it's within the valid range
    }
  };

  useEffect(() => {
    const nights = parseInt(watch("nights"), 10);
    const price = parseFloat(watch("price"));
    const precioTotal = parseFloat(watch("precioTotal"));

    if (!isNaN(nights) && nights > 0) {
      if (!isNaN(price)) {
        // Si el precio cambia, actualizamos precioTotal
        const newPrecioTotal = price * nights;
        setValue("precioTotal", newPrecioTotal);
      } else if (!isNaN(precioTotal)) {
        // Si precioTotal cambia, actualizamos precio
        const newPrice = precioTotal / nights;
        setValue("price", newPrice);
      }
    }

    // Recalculate montoPendiente
    const adelanto = parseFloat(watch("adelanto") || 0);
    const newMontoPendiente = (precioTotal || 0) - adelanto;
    setValue("montoPendiente", newMontoPendiente);
  }, [
    watch("nights"),
    watch("price"),
    watch("precioTotal"),
    watch("adelanto"),
    setValue,
  ]);
  const handlePriceChange = (newPrice) => {
    const priceValue = parseFloat(newPrice);
    if (!isNaN(priceValue) && priceValue >= 0) {
      setValue("price", priceValue);
      const nights = parseInt(watch("nights"), 10);
      if (!isNaN(nights) && nights > 0) {
        setValue("precioTotal", priceValue * nights);
      }
    }
  };

  const handlePrecioTotalChange = (newPrecioTotal) => {
    const precioTotalValue = parseFloat(newPrecioTotal);
    if (!isNaN(precioTotalValue) && precioTotalValue >= 0) {
      setValue("precioTotal", precioTotalValue);
      const nights = parseInt(watch("nights"), 10);
      if (!isNaN(nights) && nights > 0) {
        setValue("price", precioTotalValue / nights);
      }
    }
  };

  const handleNightsChange = (nights) => {
    const nightsValue = parseInt(nights, 10);
    if (nightsValue >= 1) {
      setValue("nights", nightsValue);

      // Actualizar precio total
      const price = parseFloat(watch("price"));
      if (!isNaN(price)) {
        setValue("precioTotal", price * nightsValue);
      }

      // Actualizar fecha de egreso (checkOut)
      const checkInDate = new Date(watch("checkIn"));
      if (checkInDate instanceof Date && !isNaN(checkInDate)) {
        const newCheckOutDate = new Date(checkInDate);
        newCheckOutDate.setDate(checkInDate.getDate() + nightsValue);

        // Formatear la nueva fecha de checkOut al formato esperado (YYYY-MM-DD)
        const formattedCheckOut = newCheckOutDate.toISOString().split("T")[0];
        setValue("checkOut", formattedCheckOut);
      }

      clearErrors("nights");
    } else {
      setError("nights", {
        type: "manual",
        message: "El nmero de noches debe ser al menos 1",
      });
    }
  };

  useEffect(() => {
    const roomNumber = watch("roomNumber");

    // Example validation: Check if roomNumber is within a specific range
    if (roomNumber < 1 || roomNumber > 100) {
      // Assuming rooms are numbered 1-100
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
    }
  }, [watch("roomNumber"), setError, clearErrors]);

  useEffect(() => {
    const checkInDate = new Date(watch("checkIn"));
    const checkOutDate = new Date(watch("checkOut"));
    const nights = parseInt(watch("nights"), 10);
    const expectedCheckOutDate = add(checkInDate, { days: nights });

    if (
      isAfter(checkOutDate, checkInDate) &&
      isSameDay(expectedCheckOutDate, checkOutDate)
    ) {
      // Clear errors if the conditions are met
      clearErrors(["checkIn", "checkOut", "nights"]);
    } else {
      // Set specific error messages for each case
      if (!isAfter(checkOutDate, checkInDate)) {
        setError("checkIn", {
          type: "dateRange",
          message: "Check-in date must be before check-out date.",
        });
      }
      if (!isSameDay(expectedCheckOutDate, checkOutDate)) {
        setError("nights", {
          type: "dateMismatch",
          message: "Number of nights does not match the dates selected.",
        });
      }
    }
  }, [
    watch("checkIn"),
    watch("checkOut"),
    watch("nights"),
    setError,
    clearErrors,
  ]);

  const handleMontoPendienteChange = () => {
    const precioTotal = parseFloat(watch("precioTotal") || 0);
    const adelanto = parseFloat(watch("adelanto") || 0);
    const montoPendiente = precioTotal - adelanto;
    setValue("montoPendiente", montoPendiente);
  };
  const handleCommentsChange = (value) => {
    setValue("comments", value);
  };
  const handleNameChange = (value) => {
    setValue("name", value);
  };
  const handleSurnameChange = (value) => {
    setValue("surname", value);
  };
  useEffect(() => {
    const currentComments = watch("comments");

    if (currentComments.length > 500) {
      setError("comments", {
        type: "maxLength",
        message: "Comment is too long. Maximum length is 500 characters.",
      });
    } else {
      clearErrors("comments");
    }
  }, [watch("comments"), setError, clearErrors]);

  const billingStatusOptions = [
    { value: "pendiente", label: "Pendiente" },
    { value: "pagado", label: "Pagado" },
    { value: "con-sena", label: "Con sea" },
    { value: "no-pagado", label: "No pagado" },
  ];

  const housekeepingStatusOptions = [
    { value: "pendiente", label: "Pendiente" },
    { value: "limpio", label: "Limpio" },
    { value: "reparacion", label: "Reparacin" },
    { value: "faltan-fundas", label: "Faltan fundas" },
    { value: "faltan-sabanas", label: "Faltan sbanas" },
    { value: "falta-limpiar", label: "Falta limpiar" },
  ];

  const { selectedRoomsMsg } = useContext(DatesContext);

  const handleSelectedRoomsChange = (value) => {
    setValue("selectedRooms", value);
    selectedRoomsMsg(value);
  };

  const onSubmit = async (data) => {
    const {
      checkIn,
      checkOut,
      nights,
      name,
      email,
      phone,
      selectedRooms,
      precioTotal,
      paymentMethod,
      numberOfGuests,
      guestNames,
      roomType,
      isBooking,
      surname,
      billingStatus,
      housekeepingStatus,
    } = data;

    const nightsDifference = differenceInDays(
      new Date(checkOut),
      new Date(checkIn)
    );

    if (nightsDifference !== parseInt(nights, 10)) {
      setError("nights", {
        type: "dateMismatch",
        message: "Number of nights does not match the dates selected.",
      });
      return;
    }

    const isOverlapping = intervalRentedArray.some((interval) => {
      const newStart = addDays(new Date(watch("checkIn")), 1);
      const newEnd = new Date(watch("checkOut"));
      const existingEnd = new Date(interval.start); // Este es realmente el inicio
      const existingStart = new Date(interval.end); // Este es realmente el fin

      const sameRoom = `${interval.room}` === `${watch("roomNumber")}`;

      const overlapCondition = areIntervalsOverlapping(
        { start: newStart, end: newEnd },
        { start: existingStart, end: existingEnd }
      );

      return sameRoom && overlapCondition;
    });
    console.log(isOverlapping);
    if (!isOverlapping) {
      let convertedCheckin = checkIn.split("-").join("/");
      let convertedChecOut = checkOut.split("-").join("/");

      const requestUrl = `/create-reservation?userId=${userId}`; // Use relative path

      try {
        const res = await axios.post(requestUrl, {
          reservationData: {
            name,
            email,
            phone,
            room: selectedRooms,
            start: convertedCheckin,
            end: convertedChecOut,
            price: watch("price"),
            nights,
            comments: watch("comments"),
            precioTotal,
            adelanto: watch("adelanto"),
            time: currentDateTime,
            nombre_recepcionista: watch("nombre_recepcionista"),
            montoPendiente: watch("montoPendiente"),
            dni: watch("dni"),
            paymentMethod,
            numberOfGuests,
            guestNames,
            roomType,
            isBooking,
            surname,
            billingStatus,
            housekeepingStatus,
          }
        });

        console.log('Reservation created:', res.data);
      } catch (error) {
        console.error('Error creating reservation:', error);
      }
    } else {
      // Handle the case where there is an overlapping reservation
      setError("checkIn", {
        type: "overlap",
        message: "The selected dates overlap with an existing reservation.",
      });
    }
    showFormMsg(false);
  };
  const recepcionistaOptions = [
    { value: "gonzalo", label: "Gonzalo" },
    { value: "matias", label: "Matias" },
    { value: "gabriela", label: "Gabriela" },
    { value: "daniela", label: "Daniela" },
    { value: "priscila", label: "Priscila" },
    { value: "maxi", label: "Maxi" },
  ];

  return (
    <div className="formWrapper">
      <button
        onClick={handleCloseForm}
        style={{
          color: 'wheat',
          fontSize: '20px',
          position: 'absolute',
          top: '10px',
          right: '20%',
          width: '100px',
          zIndex: 1000, // Asegurarse de que est por encima de otros elementos
          backgroundColor: 'transparent', // Fondo transparente
          border: 'none', // Sin borde
          cursor: 'pointer', // Cambiar el cursor al pasar el mouse
        }}
      >
        <CloseIcon /> {/* Icono de cerrar */}
      </button>
      <form onSubmit={handleSubmit(onSubmit)} className="formContainer">
        <div className="formColumn">
          <h3>Informacin del Husped</h3>
          <InputForm control={control} name="name" type="text" label="Nombre" value={watch("name")} handleChange={handleNameChange} />
          <InputForm control={control} name="surname" type="text" label="Apellido" value={watch("surname")} handleChange={handleSurnameChange} />
          <InputForm control={control} name="email" type="email" label="Email" value={watch("email")} handleChange={handleEmailChange} />
          <InputForm control={control} name="phone" type="tel" label="Telfono" value={watch("phone")} handleChange={(value) => setValue("phone", value)} />
          <InputForm control={control} name="dni" type="text" label="DNI" value={watch("dni")} handleChange={(value) => setValue("dni", value)} />
        </div>

        <div className="formColumn">
          <h3>Detalles de la Reserva</h3>
          <InputForm control={control} name="checkIn" type="date" label="Fecha de Entrada" value={watch("checkIn")} handleChange={handleCheckInChange} />
          <InputForm control={control} name="checkOut" type="date" label="Fecha de Salida" value={watch("checkOut")} handleChange={handleCheckOutChange} />
          <InputForm control={control} name="nights" type="number" label="Nmero de Noches" value={watch("nights")} handleChange={handleNightsChange} />
          <InputForm control={control} name="numberOfGuests" type="number" label="Nmero de huspedes" value={watch("numberOfGuests")} handleChange={handleNumberOfGuestsChange} onGuestInfoClick={handleGuestInfoClick} />
          <InputForm
            control={control}
            name="selectedRooms"
            type="select"
            label="Habitaciones"
            value={watch("selectedRooms")}
            options={Array.from({ length: 15 }, (_, i) => ({ value: i + 1, label: i + 1 }))}
            handleChange={handleSelectedRoomsChange}
            multiple={true}
            renderValue={(selected) => (
              <div className="selectedRoomsContainer">
                {selected.map((value) => (
                  <Chip key={value} label={value} className="roomChip" />
                ))}
              </div>
            )}
          />
          <InputForm
            control={control}
            name="roomType"
            type="select"
            label="Tipo de Habitacin"
            value={watch("roomType")}
            options={[
              { value: "individual", label: "Individual" },
              { value: "doble", label: "Doble" },
              { value: "triple", label: "Triple" },
              { value: "cuadruple", label: "Cuadruple" }
            ]}
            handleChange={(value) => handleRoomTypeChange(value, false)}
          />
        </div>

        <div className="formColumn">
          <h3>Pago y Habitacin</h3>
          <InputForm control={control} name="price" type="number" label="Precio" value={watch("price")} readOnly handleChange={() => { }} />
          <InputForm control={control} name="precioTotal" type="number" label="Precio Total" value={watch("precioTotal")} handleChange={handlePrecioTotalChange} />
          <InputForm control={control} name="adelanto" type="number" label="Adelanto" value={watch("adelanto")} handleChange={handleadelantoChange} />
          <InputForm control={control} name="montoPendiente" type="number" label="Monto Pendiente" value={watch("montoPendiente")} readOnly handleChange={handleMontoPendienteChange} />
          <InputForm
            control={control}
            name="paymentMethod"
            type="select"
            label="Mtodo de Pago"
            value={watch("paymentMethod")}
            options={[
              { value: "efectivo", label: "Efectivo" },
              { value: "tarjeta", label: "Tarjeta" },
              { value: "transferencia", label: "Transferencia" }
            ]}
            handleChange={handlePaymentMethodChange}
          />
        </div>

        <div className="formRow fullWidth">
          <InputForm control={control} name="billingStatus" type="select" label="Estado de Facturacin" value={watch("billingStatus")} options={billingStatusOptions} handleChange={(value) => handleBillingStatusChange(value)} />
          <InputForm control={control} name="housekeepingStatus" type="select" label="Estado de Limpieza" value={watch("housekeepingStatus")} options={housekeepingStatusOptions} handleChange={(value) => handleHousekeepingStatusChange(value)} />
          <InputForm control={control} name="nombre_recepcionista" type="select" label="Nombre Recepcionista" value={watch("nombre_recepcionista")} options={recepcionistaOptions} handleChange={handleNombreRecepcionistaChange} />
        </div>

        <div className="formRow fullWidth comments-section">
          <InputForm control={control} name="comments" type="textarea" label="Comentarios" multiline rows={3} value={watch("comments")} handleChange={handleCommentsChange} />
        </div>

        <div className="submit-button-container fullWidth">
          <button className="submit-button" type="submit">Confirmar</button>
        </div>
      </form>
    </div>
  );
};
